"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

export default function CheckInPage() {
  const [staffId, setStaffId] = useState("")
  const [status, setStatus] = useState<{ type: "success" | "error", message: string } | null>(null)
  const [isCheckedIn, setIsCheckedIn] = useState(false)

  const handleCheckIn = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      // In a real app, this would be an API call
      if (staffId.trim()) {
        setIsCheckedIn(true)
        setStatus({
          type: "success",
          message: `Kehadiran berjaya didaftarkan pada ${new Date().toLocaleString('ms-MY')}`
        })
      } else {
        setStatus({
          type: "error",
          message: "Sila masukkan ID Penyelia"
        })
      }
    } catch (error) {
      setStatus({
        type: "error",
        message: "Ralat semasa mendaftar kehadiran. Sila cuba lagi."
      })
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 flex items-center justify-center p-4">
      <Card className="w-full max-w-md p-6 bg-white/10 backdrop-blur-lg rounded-lg shadow-xl">
        <h1 className="text-3xl font-bold text-center text-white mb-8">
          Daftar Kehadiran
        </h1>
        
        {!isCheckedIn ? (
          <form onSubmit={handleCheckIn} className="space-y-6">
            <div className="space-y-2">
              <Label className="text-white" htmlFor="staffId">ID Penyelia</Label>
              <Input
                id="staffId"
                type="text"
                placeholder="Masukkan ID Penyelia"
                value={staffId}
                onChange={(e) => setStaffId(e.target.value)}
                className="bg-white/20 text-white placeholder:text-gray-400"
                required
              />
            </div>
            <Button type="submit" className="w-full py-6 text-lg">
              Daftar Kehadiran
            </Button>
          </form>
        ) : (
          <div className="text-center space-y-4">
            <div className="text-green-400 text-xl font-semibold">
              âœ“ Kehadiran Berjaya Didaftarkan
            </div>
            <Button 
              onClick={() => {
                setIsCheckedIn(false)
                setStaffId("")
                setStatus(null)
              }}
              variant="outline"
              className="w-full py-6 text-lg"
            >
              Daftar Kehadiran Baru
            </Button>
          </div>
        )}

        {status && (
          <p className={`mt-4 text-center ${
            status.type === "success" ? "text-green-400" : "text-red-400"
          }`}>
            {status.message}
          </p>
        )}
      </Card>
    </main>
  )
}
